          <input type="text" id="search" class="form-control" v-model="searchQuery" placeholder="Buscar por cualquier caracterstica">
        </div>
        <div class="col-md-2 ms-2 align-self-center">
          <br>
          <button @click="addArticle" class="btn btn-success">Agregar artculo</button>
