<script>
import Swal from 'sweetalert2';
import axios from "axios";
import {API_URL} from "@/config";

export default {
  name: 'ArticleList',
  data() {
    return {
      items: [],
      searchQuery: '',
    };
  },
  computed: {
    filteredArticles() {
      return this.items.filter((item) => {
        const search = this.searchQuery.toLowerCase();
        return (
            item.name.toLowerCase().includes(search) ||
            item.id.toLowerCase().includes(search) ||
            item.conversion_factor.toLowerCase().includes(search) ||
            item.minimal_stock.toLowerCase().includes(search) ||
            item.status.toLowerCase().includes(search) ||
            item.subcategory_name.toLowerCase().includes(search) ||
            item.category_name.toLowerCase().includes(search) ||
            (item.brand_name != null && item.brand_name.toLowerCase().includes(search)) ||
            (item.design_name != null && item.design_name.toLowerCase().includes(search))
        );
      });
    }
  },
  methods: {

    addArticle() {
      // Redireccionar a la vista de agregar artculo
      this.$router.push({name: 'AddArticle'});
    },
    viewItem(/*article*/) {
      // Lgica para ver los detalles de un artculo
      // ...
    },
    deleteArticle(article) {
      // Lgica para eliminar un artculo
      Swal.fire({
        title: 'Ests seguro?',
        text: `Se eliminar el artculo ${article.name}. Esta accin no se puede deshacer.`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#d33',
        cancelButtonColor: '#3085d6',
        confirmButtonText: 'S, eliminar',
        cancelButtonText: 'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          // Lgica para eliminar el artculo aqu
          // ...

          Swal.fire({
            title: 'Eliminado',
            text: 'El artculo ha sido eliminado correctamente.',
            icon: 'success',
            confirmButtonText: 'Aceptar'
          });
        }
      });
    },
  },
  mounted() {
    this.$state.navbarTitle = 'Lista de Artculos';
    // Obtener todos los artculos desde la API
    axios.get(API_URL + '/items').then(response => {
      console.log(response.data);
      this.items = response.data.items;
    }).catch(error => {
      console.log(error);
    });
  },
};
</script>

