<template>
  <div class="container">
    <div class="row">
      <div class="col-md-8 offset-md-2">
        <div class="mb-3">
          <label for="search" class="form-label">Buscar categora:</label>
          <input type="text" id="search" class="form-control" v-model="searchQuery" placeholder="Buscar por nombre">
        </div>
        <div class="row">
          <div class="col-md-12">
            <button @click="addCategory" class="btn btn-success">Agregar categora</button>
          </div>
        </div>
        <div class="table-responsive">
          <table class="table table-striped table-responsive">
            <thead>
            <tr>
              <th class="text-center">Nombre de Categora</th>
              <th class="text-center">Acciones</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="category in filteredCategories" :key="category.name">
              <td class="text-center">{{ category.name }}</td>
              <td class="text-center">
                <button @click="editCategory(category)" class="btn btn-primary">Editar categora</button>
                <button @click="deleteCategory(category)" class="btn btn-danger">Eliminar</button>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { API_URL } from "@/config";
import Swal from 'sweetalert2';

export default {
  name: 'CategoryList',
  data() {
    return {
      categories: [],
      searchQuery: '',
    };
  },
  computed: {
    filteredCategories() {
      return this.categories.filter((category) => {
        return category.name.toLowerCase().includes(this.searchQuery.toLowerCase());
      });
    }
  },
  methods: {
    addCategory() {
      // Redirigir a la vista de agregar categora
      this.$router.push({ name: 'AddCategory' });
    },
    editCategory(category) {
      // Redirigir a la vista de editar categora
      this.$router.push({ name: 'EditCategory', params: { id: category.id } });
    },
    deleteCategory(category) {
      // Lgica para eliminar una categora
      Swal.fire({
        title: 'Ests seguro?',
        text: `Se eliminar la categora ${category.name}. Esta accin no se puede deshacer.`,
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#d33',
        cancelButtonColor: '#3085d6',
        confirmButtonText: 'S, eliminar',
        cancelButtonText: 'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          // Lgica para eliminar la categora aqu
          // ...
          this.categories = this.categories.filter((c) => c.id !== category.id);

          Swal.fire({
            title: 'Eliminado',
            text: 'La categora ha sido eliminada correctamente.',
            icon: 'success',
            confirmButtonText: 'Aceptar'
          });
        }
      });
    },
  },
  mounted() {
    this.$state.navbarTitle = 'Lista de Categoras';
    // Obtener todas las categoras desde la API
    axios.get(API_URL + '/categories').then(response => {
      this.categories = response.data;
    }).catch(error => {
      console.log(error);
    });
  },
};
</script>

