<template>
  <div class="container">
    <form @submit.prevent="onCreateSubcategory">
      <div class="form-group mt-4">
        <label for="subCategoryName">Ingrese el nombre de la subcategora:</label>
        <input type="text" class="form-control" id="subcategory" v-model="name">
      </div>
      <div class="form-group mt-4">
        <label for="category">Seleccione la categora a la que se asociar esta subcategora:</label>
        <select class="form-control" id="category" v-model="category">
          <option value="" disabled selected>Seleccione una categora</option>
          <option v-for="category in categories" :key="category.id" :value="category.id">{{ category.name }}</option>
        </select>
      </div>
      <div class="d-flex justify-content-end">
        <button class="btn btn-primary" type="submit">Guardar cambios</button>
        <router-link to="/subcategories" class="btn btn-danger">Cancelar</router-link>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";
import {API_URL} from '@/config'

export default {
  data() {
    return {
      name: '',
      categories: []
    };
  },
  mounted() {
    this.$state.navbarTitle = 'Agregar Nueva Subcategora';
    axios.get(API_URL + '/categories').then(response => {
      this.categories = response.data;
    }).catch(error => {
      console.log(error);
    });
  },
  methods: {
    onCreateSubcategory() {
      let subcategory = {
        subcategory: {
          name: this.name,
          category_id: this.category
        }
      };
      subcategory.user = this.$store.state.user.id;
      axios.post(API_URL + '/subcategories', subcategory).then(response => {
        Swal.fire({
          title: 'Subcategora creada exitosamente',
          icon: 'success',
          showConfirmButton: false,
          timer: 1500
        });
        console.log(response);

      }).catch(error => {
        // Show error message
        Swal.fire({
          title: 'Error al crear la subcategora',
          icon: 'error',
          showConfirmButton: false,
          timer: 1500
        });
        // Shows why the error was thrown using sweetalert2
        const errors = error.response.data;
        console.log(errors);
      });
    },
  }, 
};
</script>

